@import './mixins.scss';
@import './variables.scss';
body * {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
	margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 16px;
  background-color: $white;
  color: #909090;
}

li {
  list-style-type: none;
  font-size: 16px;
  letter-spacing: 0.5px;
}
a:active,
a {
  text-decoration: none;
  color: $black;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

p {
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.4px;
}

/* Main page */
h1 {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: 0.8px;
}

h2 {
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 1.5px;
}

.main-page {
  display: flex;
  flex-grow: 1;
  align-items: center;
  >div {
    width: 50%;
  }
}

.left-article {
  display: flex;
  flex-direction: column;
  align-items: center;
  .site-title {
    flex-direction: column;
    max-width: 368px;
    text-align: center;
    padding-bottom: 52px;
    h1 {
      color: #000;
      padding-bottom: 20px;
    }
  }
  button {
    @include main-button(158px, 52px);
  }
}

.right-article {
  display: flex;
  position: relative;
  justify-content: center;
  overflow: hidden;
  max-height: 756px;
}

.main-logo {
  @include flex-center;
  padding-bottom: 50px;
  h2 {
    color: $black;
    font-weight: bold;
    padding-left: 8px;
    text-transform: uppercase;
  }
}

.header {
  width: 50%;
  position: absolute;
  right: 0;
  display: flex;
  justify-content: center;
  z-index: 999;
  padding: 18px 56px 86px;
  color: $black;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 100%);
  .main-logo {
    display: none;
  }
  nav {
    top: 0;
    ul {
      display: flex;
      li {
        padding-right: 32px;
        line-height: 24px;
      }
    }
  }
  .sign-in {
    @include flex-center;
    span {
      display: none;
    }
  }
  li.current_page_item > a,
  li > a:hover,
  li:hover,
  .sign-in:hover {
    color: $gray;
    text-decoration: underline;
    cursor: pointer;
  }
}

.header-static {
  width: 100%;
  position: static;
  justify-content: space-between;
  background: $white;
  padding: 18px 56px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08), 0px 2px 8px rgba(0, 0, 0, 0.04);
  animation-name: show_header;
  animation-duration: 0.5s;
  .main-logo {
    @include flex-center;
    padding-bottom: 0;
  }
  .sign-in {
    @include flex-center;
    span {
      display: inline;
      margin-right: 12px;
    }
  }
}

.hide {
  position: fixed;
}

@keyframes show_header {
  0%{
    opacity: 0;
    top: -60px;
  }
  100% {
    opacity: 1;
    top: 0;
  }  
}

