@mixin flex-column {
  display: flex;
  flex-direction: column;
}
@mixin flex-center {
  display: flex;
  align-items: center;
}
@mixin main-button($width, $height) {
  background: #000000;
  color: #FFFFFF;
  border-radius: 4px;
  width: $width;
  height: $height;
  border: none;
  span {
    padding: 14px 20px;
    letter-spacing: 1px;
    text-transform: uppercase;
  }
}
@mixin big-title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: 0.8px;
}
@mixin middle-title {
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.5px;
}
@mixin small-title {
  font-size: 14px;
  line-height: 20px;
}
