@import './mixins.scss';
.how-to-get {
  display: flex;
  max-height: 756px;
  > div {
    width: 50%;
    overflow: hidden;
  }
  .steps {
    @include flex-center;
    flex-direction: column;
    color: #000;
    padding: 136px;
    > p {
      text-align: start;
      font-size: 32px;
      line-height: 40px;
      letter-spacing: 0.8px;
      padding-bottom: 52px;
    }
    .list {
      @include flex-center;
      position: relative;
      height: 48px;
      margin-bottom: 32px;
      p {
        text-align: start;
        width: 268px;
        margin-left: 56px;
        letter-spacing: 0.5px;
      }
    }
    .button-left {
      display: flex;
      justify-content: flex-start;
      width: 100%;
      button {
        @include main-button(158px, 52px);
      }
    }
  }
}

.square {
  display: block;
  position: absolute;
  text-align: center;
  line-height: 32px;
  width: 32px;
  height: 32px;
  border: 2px solid black;
  border-radius: 4px;
}