@import './mixins.scss';
@import './variables';
footer {
  display: flex;
  background: rgba(0, 0, 0, 0.04);
  >div {
    width: 50%;
    display: flex;
    justify-content: space-between;
    margin: 80px 30px;
    color: $black;
    .contacts {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-left: 26px;
      .title {
        @include middle-title;
        font-weight: 600;
      }
      .email {
        font-size: 18px;
        line-height: 32px;
        letter-spacing: 0.4px;
        color: $gray;
        margin: 24px 0;
      }
      button {
        margin-top: 58px;
        @include main-button(191px, 52px);
        span {
          padding: 0;
        }
      }
    }
    .footer-menu {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      li {
        padding-bottom: 20px;
        line-height: 24px;
      }
      a {
        text-decoration: underline;
        text-transform: uppercase;
        letter-spacing: 1px;
        line-height: 24px;
      }
      li:hover,
      a:hover {
        text-decoration: underline;
        color: $gray;
        cursor: pointer;
      }
    }
    .payment {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-end;
      margin-right: 26px;
      .hz {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        p {
          margin-bottom: 20px;
        }
      }
      p {
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.4px;
        color: $gray;
      }
    }
  }
}