@import './mixins.scss';
.feedback {
  display: flex;
  max-height: 788px;
  justify-content: center;
  > div {
    width: 50%;
  }
  .left-column {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 120px;
    padding-bottom: 120px;
    > div {
      padding: 40px;
      display: flex;
      flex-direction: column;
      border: 2px solid black;
      border-radius: 4px;
      align-items: flex-start;
      .image-feedback {
        display: flex;
        align-items: center;
        width: 328px;
        height: 328px;
        margin-bottom: 20px;
        img {
          max-width: 100%;
          height: auto;
        }
      }
      .feedback-label {
        @include small-title;
        color: #FFF;
        background-color: #000;
        border: 1px solid #000;
        border-radius: 1px;
        padding: 2px 4px;
      }
      .feedback-title {
        margin: 8px 0;
        color: #000;
        @include middle-title();
        font-weight: 600;
        max-width: 328px;
        text-align: start;
      }
    }
  }
  .right-column {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #000;
    >div {
      max-width: 468px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      p {
        text-align: start;
      }
      .feedback-header {
        @include big-title;
        margin-bottom: 52px;
      }
      .feedback-content {
        @include middle-title;
        line-height: 32px;
        font-style: italic;
        margin-bottom: 36px;
      }
      div {
        @include flex-center;
        img {
          margin-right: 15px;
        }
        p {
          color: #909090;
          letter-spacing: 0.5px;
          span {
            color: #000;
            font-weight: 600;
          }
        }
      }
      button {
        margin-top: 52px;
        @include main-button(102px, 52px);
        background: #FFF;
        color: #000;
        border: 2px solid #000;
      }
    }
  }
}