@import './mixins.scss';
.brands {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  h2 {
    text-transform: uppercase;
    @include middle-title;
    margin-top: 120px;
    margin-bottom: 56px;
  }
  .carousel {
    display: flex;
    align-items: center;
    .companies {
      display: flex;
      justify-content: space-around;
      flex-grow: 1;
      margin-bottom: 120px;
      overflow: hidden;
      >div {
        display: flex;
        width: 25%;
        flex-direction: column;
        align-items: flex-start;
        margin: 0 50px;
        color: #000;
        div {
          display: flex;
          justify-content: flex-start;
          width: 120px;
          height: 80px;
          margin-bottom: 20px;
          img {
            max-width: 100%;
            height: auto;
          }
        }
        :last-child {
          font-size: 12px;
          line-height: 16px;
          letter-spacing: 0.4px;
          color: #999999;
        }
        p {
          text-align: start;
        }
        .brands-description {
          margin: 12px 0;
        }
      }
      div:hover,
      a:hover {
        text-decoration: underline;
        cursor: pointer;
      }
    }
    .arrow {
      font-size: 30px;
      cursor: pointer;
      z-index: 10;
    }
    .arrow-left {
      margin-left: 36px;
    }
    .arrow-right {
      margin-right: 36px;
    }
  }
}

