.design_concept {
  text-align: center;
  padding-top: 120px;
  background: #FBFBFB;
  .blocks-in-row {
    display: flex;
    justify-content: space-between;
    padding: 56px 56px 120px 56px;
    div {
      display: flex;
      flex-direction: column;;
      align-items: center;
      width: 30%;
      div {
        padding-top: 25px;
        width: 100%;
        max-width: 300px;
        h2 {
          padding-bottom: 20px;
          text-transform: none;
        }
      }
    }
  }
  h2 {
    text-transform: uppercase;
    color: #000;
  }
}
