body * {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 16px;
  background-color: #FFF;
  color: #909090;
}

li {
  list-style-type: none;
  font-size: 16px;
  letter-spacing: 0.5px;
}

a:active,
a {
  text-decoration: none;
  color: #000;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

p {
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.4px;
}

/* Main page */
h1 {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: 0.8px;
}

h2 {
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 1.5px;
}

.main-page {
  display: flex;
  flex-grow: 1;
  align-items: center;
}

.main-page > div {
  width: 50%;
}

.left-article {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.left-article .site-title {
  flex-direction: column;
  max-width: 368px;
  text-align: center;
  padding-bottom: 52px;
}

.left-article .site-title h1 {
  color: #000;
  padding-bottom: 20px;
}

.left-article button {
  background: #000000;
  color: #FFFFFF;
  border-radius: 4px;
  width: 158px;
  height: 52px;
  border: none;
}

.left-article button span {
  padding: 14px 20px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.right-article {
  display: flex;
  position: relative;
  justify-content: center;
  overflow: hidden;
  max-height: 756px;
}

.main-logo {
  display: flex;
  align-items: center;
  padding-bottom: 50px;
}

.main-logo h2 {
  color: #000;
  font-weight: bold;
  padding-left: 8px;
  text-transform: uppercase;
}

.header {
  width: 50%;
  position: absolute;
  right: 0;
  display: flex;
  justify-content: center;
  z-index: 999;
  padding: 18px 56px 86px;
  color: #000;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 100%);
}

.header .main-logo {
  display: none;
}

.header nav {
  top: 0;
}

.header nav ul {
  display: flex;
}

.header nav ul li {
  padding-right: 32px;
  line-height: 24px;
}

.header .sign-in {
  display: flex;
  align-items: center;
}

.header .sign-in span {
  display: none;
}

.header li.current_page_item > a,
.header li > a:hover,
.header li:hover,
.header .sign-in:hover {
  color: #999999;
  text-decoration: underline;
  cursor: pointer;
}

.header-static {
  width: 100%;
  position: static;
  justify-content: space-between;
  background: #FFF;
  padding: 18px 56px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08), 0px 2px 8px rgba(0, 0, 0, 0.04);
  animation-name: show_header;
  animation-duration: 0.5s;
}

.header-static .main-logo {
  display: flex;
  align-items: center;
  padding-bottom: 0;
}

.header-static .sign-in {
  display: flex;
  align-items: center;
}

.header-static .sign-in span {
  display: inline;
  margin-right: 12px;
}

.hide {
  position: fixed;
}

@keyframes show_header {
  0% {
    opacity: 0;
    top: -60px;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}

.design_concept {
  text-align: center;
  padding-top: 120px;
  background: #FBFBFB;
}

.design_concept .blocks-in-row {
  display: flex;
  justify-content: space-between;
  padding: 56px 56px 120px 56px;
}

.design_concept .blocks-in-row div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
}

.design_concept .blocks-in-row div div {
  padding-top: 25px;
  width: 100%;
  max-width: 300px;
}

.design_concept .blocks-in-row div div h2 {
  padding-bottom: 20px;
  text-transform: none;
}

.design_concept h2 {
  text-transform: uppercase;
  color: #000;
}

.how-to-get {
  display: flex;
  max-height: 756px;
}

.how-to-get > div {
  width: 50%;
  overflow: hidden;
}

.how-to-get .steps {
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #000;
  padding: 136px;
}

.how-to-get .steps > p {
  text-align: start;
  font-size: 32px;
  line-height: 40px;
  letter-spacing: 0.8px;
  padding-bottom: 52px;
}

.how-to-get .steps .list {
  display: flex;
  align-items: center;
  position: relative;
  height: 48px;
  margin-bottom: 32px;
}

.how-to-get .steps .list p {
  text-align: start;
  width: 268px;
  margin-left: 56px;
  letter-spacing: 0.5px;
}

.how-to-get .steps .button-left {
  display: flex;
  justify-content: flex-start;
  width: 100%;
}

.how-to-get .steps .button-left button {
  background: #000000;
  color: #FFFFFF;
  border-radius: 4px;
  width: 158px;
  height: 52px;
  border: none;
}

.how-to-get .steps .button-left button span {
  padding: 14px 20px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.square {
  display: block;
  position: absolute;
  text-align: center;
  line-height: 32px;
  width: 32px;
  height: 32px;
  border: 2px solid black;
  border-radius: 4px;
}

.principles .blocks-in-row {
  justify-content: center;
}

.principles .blocks-in-row div {
  max-width: 209px;
  margin: 0 16px;
}

.principles .blocks-in-row div div h2 {
  font-size: 16px;
}

.feedback {
  display: flex;
  max-height: 788px;
  justify-content: center;
}

.feedback > div {
  width: 50%;
}

.feedback .left-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 120px;
  padding-bottom: 120px;
}

.feedback .left-column > div {
  padding: 40px;
  display: flex;
  flex-direction: column;
  border: 2px solid black;
  border-radius: 4px;
  align-items: flex-start;
}

.feedback .left-column > div .image-feedback {
  display: flex;
  align-items: center;
  width: 328px;
  height: 328px;
  margin-bottom: 20px;
}

.feedback .left-column > div .image-feedback img {
  max-width: 100%;
  height: auto;
}

.feedback .left-column > div .feedback-label {
  font-size: 14px;
  line-height: 20px;
  color: #FFF;
  background-color: #000;
  border: 1px solid #000;
  border-radius: 1px;
  padding: 2px 4px;
}

.feedback .left-column > div .feedback-title {
  margin: 8px 0;
  color: #000;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.5px;
  font-weight: 600;
  max-width: 328px;
  text-align: start;
}

.feedback .right-column {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000;
}

.feedback .right-column > div {
  max-width: 468px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.feedback .right-column > div p {
  text-align: start;
}

.feedback .right-column > div .feedback-header {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: 0.8px;
  margin-bottom: 52px;
}

.feedback .right-column > div .feedback-content {
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.5px;
  line-height: 32px;
  font-style: italic;
  margin-bottom: 36px;
}

.feedback .right-column > div div {
  display: flex;
  align-items: center;
}

.feedback .right-column > div div img {
  margin-right: 15px;
}

.feedback .right-column > div div p {
  color: #909090;
  letter-spacing: 0.5px;
}

.feedback .right-column > div div p span {
  color: #000;
  font-weight: 600;
}

.feedback .right-column > div button {
  margin-top: 52px;
  background: #000000;
  color: #FFFFFF;
  border-radius: 4px;
  width: 102px;
  height: 52px;
  border: none;
  background: #FFF;
  color: #000;
  border: 2px solid #000;
}

.feedback .right-column > div button span {
  padding: 14px 20px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.brands {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.brands h2 {
  text-transform: uppercase;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.5px;
  margin-top: 120px;
  margin-bottom: 56px;
}

.brands .carousel {
  display: flex;
  align-items: center;
}

.brands .carousel .companies {
  display: flex;
  justify-content: space-around;
  flex-grow: 1;
  margin-bottom: 120px;
  overflow: hidden;
}

.brands .carousel .companies > div {
  display: flex;
  width: 25%;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 50px;
  color: #000;
}

.brands .carousel .companies > div div {
  display: flex;
  justify-content: flex-start;
  width: 120px;
  height: 80px;
  margin-bottom: 20px;
}

.brands .carousel .companies > div div img {
  max-width: 100%;
  height: auto;
}

.brands .carousel .companies > div :last-child {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.4px;
  color: #999999;
}

.brands .carousel .companies > div p {
  text-align: start;
}

.brands .carousel .companies > div .brands-description {
  margin: 12px 0;
}

.brands .carousel .companies div:hover,
.brands .carousel .companies a:hover {
  text-decoration: underline;
  cursor: pointer;
}

.brands .carousel .arrow {
  font-size: 30px;
  cursor: pointer;
  z-index: 10;
}

.brands .carousel .arrow-left {
  margin-left: 36px;
}

.brands .carousel .arrow-right {
  margin-right: 36px;
}

footer {
  display: flex;
  background: rgba(0, 0, 0, 0.04);
}

footer > div {
  width: 50%;
  display: flex;
  justify-content: space-between;
  margin: 80px 30px;
  color: #000;
}

footer > div .contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 26px;
}

footer > div .contacts .title {
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.5px;
  font-weight: 600;
}

footer > div .contacts .email {
  font-size: 18px;
  line-height: 32px;
  letter-spacing: 0.4px;
  color: #999999;
  margin: 24px 0;
}

footer > div .contacts button {
  margin-top: 58px;
  background: #000000;
  color: #FFFFFF;
  border-radius: 4px;
  width: 191px;
  height: 52px;
  border: none;
}

footer > div .contacts button span {
  padding: 14px 20px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

footer > div .contacts button span {
  padding: 0;
}

footer > div .footer-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

footer > div .footer-menu li {
  padding-bottom: 20px;
  line-height: 24px;
}

footer > div .footer-menu a {
  text-decoration: underline;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 24px;
}

footer > div .footer-menu li:hover,
footer > div .footer-menu a:hover {
  text-decoration: underline;
  color: #999999;
  cursor: pointer;
}

footer > div .payment {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  margin-right: 26px;
}

footer > div .payment .hz {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

footer > div .payment .hz p {
  margin-bottom: 20px;
}

footer > div .payment p {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.4px;
  color: #999999;
}
